
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

body,html{
	min-width: 500px;
	height: 100%;
	overflow: hidden;
	background-color: #000000;
}
ul{
	padding: 0;
}
li{
	list-style: none;
	font-size: 1.2em;
	height: 65px;
}
.lry-title{
	color: #409EFF;
}
.lry-list li{
	-webkit-transition: 0.5s all ease-out;
	transition: 0.5s all ease-out;
}
.lry-list li.active{
	font-size: 1.5em;
	color: #F56C6C;
}
.audioLryList-enter{
	opacity: 0;
	-webkit-transform: translateY(30px);
	        transform: translateY(30px);
}
.audioLryList-leave-to{
	opacity: 0;
	-webkit-transform: translateY(-30px);
	        transform: translateY(-30px);
}
.audioLryList-leave-active {
	/* position: absolute; */
}
button{
	outline: none;
	padding: 5px 15px;
	border: 1px solid #CCCCCC;
	background-color: #ffffff;
	border-radius: 5px;
}
button:active{
	-webkit-opacity: 0.8;
	opacity: 0.8;
}
